<template>
  <div class="notification-page">
    <h1>消息通知</h1>
    <div class="notification-container">
      <div
        class="notification-card"
        v-for="section in sections"
        :key="section.title"
        :style="{ backgroundColor: section.color }"
        @click="navigateTo(section.route)"
      >
        <h2>{{ section.title }}</h2>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      sections: [
        {
          title: '志愿者招募',
          color: '#6a82fb',
          route: '/volunteer'
        },
        {
          title: '工作招聘',
          color: '#fc5c7d',
          route: '/job'
        },
        {
          title: '失物招领',
          color: '#ffc107',
          route: '/lost'
        },
        {
          title: '其他事项',
          color: '#17a2b8',
          route: '/other'
        },
        {
          title: '紧急通知',
          color: '#dc3545',
          route: '/emergency-notice'
        },
        {
          title: '安全专栏',
          color: '#28a745',
          route: '/safety-column'
        }
      ]
    };
  },
  methods: {
    navigateTo(route) {
      this.$router.push(route);
    }
  }
};
</script>

<style scoped>
.notification-page {
  text-align: center;
  padding: 20px;
  background: linear-gradient(135deg, #9360a5, #5f1896); /* 漂亮的渐变背景 */
  min-height: 100vh;
}

.notification-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 20px; /* 卡片之间的间距 */
}

.notification-card {
  width: 150px; /* 卡片宽度 */
  height: 150px; /* 卡片高度 */
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s;
  position: relative;
  animation: swing 1s infinite; /* 添加摆动动画 */
}

@keyframes swing {
  0% { transform: rotate(0deg); }
  25% { transform: rotate(2deg); }
  50% { transform: rotate(0deg); }
  75% { transform: rotate(-2deg); }
  100% { transform: rotate(0deg); }
}

.notification-card:hover {
  transform: scale(1.1) rotate(3deg); /* 悬停时放大和旋转 */
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.notification-card:nth-child(odd) {
  transform: rotate(-2deg); /* 奇数卡片稍微旋转 */
}

.notification-card:nth-child(even) {
  transform: rotate(2deg); /* 偶数卡片稍微旋转 */
}
</style>



